import './index.css';
import { createBrowserRouter, RouterProvider } from 'react-router-dom';
import Navigation from './components/Navigation/Navigation';
import SearchBoard from './pages/SerachBoard/SearchBoard';
import Course from './pages/Course/Course';
import Country from './pages/Country/Country';
import { Children } from 'react';
import Institutions from './pages/Country/Institutions';
import CountryCourses from './pages/Country/Coursers';
import Univercity from './pages/Univercity/Univercity';
import Explore from './pages/Explore/Explore';

const router = createBrowserRouter([{
  path: '/',
  element: <Navigation />,
  children: [{
    path: '/',
    element: <SearchBoard />
  },{
    path: '/country/:country',
    element: <Country />,
    children: [{
      path: '/country/:country',
      element: <Institutions />
    },{
      path: '/country/:country/courses',
      element: <CountryCourses />
    }]
  },{
    path: '/courses/course/:id',
    element: <Course />
  },{
    path: '/country/:country/:id',
    element: <Univercity />
  },{
    path: 'explore',
    element: <Explore />
  }]
}])

function App() {
  return (
    <div className="App">
      <RouterProvider router={router} />
    </div>
  );
}

export default App;
