const api = axios.create({
    baseURL: 'http://139.59.25.83:5000',
    headers: {
        "Content-Type": "application/json",
        Accept: "application.json"
    }
})

export const getAllCourse = async (page) => api.get(`/api/courses/allcourses?page=${page}`);
export const filterCourse = async (queryStr) => api.get(`/api/courses/filtercourse${queryStr}`);
export const getAllUnivercityByCountry = async (page, country) => api.get(`/api/universities/filterunivercity?page=${page}&university_location=${country}`);
export const getUnivercityById = async (id) => api.get(`/api/universities/filterunivercity?id=${id}`);

export const getFieldOfStudy = async () => api.get('/api/fields/fiedldofstudy');
export const getAreaOfStudy = async (title) => api.get(`/api/fields/areaofstudy?title=${title}`);
export const getCourseTypes = async () => api.get('/api/fields/coursetype');
export const getCourseLevels = async () => api.get('/api/fields/courselevels')

export const getEstCost = async () => api.get('/api/courses/estimatedcost')



