import { Outlet } from "react-router-dom";

const Navigation = () => {
    return (
        <div className="navigation">
          <div className="px-3 w-full flex justify-between items-center shadow-md">
             <div className='left flex h-[60px] items-center'>
                <div className='logo mr-8'>
                   <img src='/assets/images/logo.png' alt="logo" className="h-[50px]" />
                </div>
                <div className='menu hidden md:block'>
                    <ul className='flex items-center h-[60px]'>
                        <li 
                         className='h-[60px]
                          relative 
                          flex 
                          items-center 
                          mx-8 w-[120px] 
                          justify-center'
                          
                          >
                            <span 
                             className='text-md 
                             font-semibold 
                             text-[#0A1C2B] 
                             opacity-[1] 
                             hover:opacity-[1] 
                             cursor-pointer'
                            
                             >Search Board</span>
                            <span 
                             className='opacity-[1] 
                             absolute w-[100%] 
                             h-[5px] 
                             rounded-md 
                             bg-primary 
                             flex 
                             bottom-0'
                             
                             ></span>
                        </li>
                        <li 
                         className='h-[60px]
                          relative 
                          flex 
                          items-center 
                          mx-8 w-[120px] 
                          justify-center'
                          
                          >
                            <span 
                             className='text-md 
                             font-semibold 
                             text-[#0A1C2B] 
                             opacity-[.49] 
                             hover:opacity-[1] 
                             cursor-pointer'
                             
                             >Saved Course</span>
                            <span 
                             className='opacity-[0] 
                             absolute w-[100%] 
                             h-[5px] 
                             rounded-md 
                             bg-primary 
                             flex 
                             bottom-0'
                            
                             ></span>
                        </li>
                        <li 
                         className='h-[60px]
                          relative 
                          flex 
                          items-center 
                          mx-8 w-[120px] 
                          justify-center'
                          
                          >
                            <span 
                             className='text-md 
                             font-semibold 
                             text-[#0A1C2B] 
                             opacity-[.49] 
                             hover:opacity-[1] 
                             cursor-pointer'
                             
                             >My Application</span>
                            <span 
                             className='
                             absolute w-[100%] 
                             h-[5px] 
                             rounded-md 
                             bg-primary 
                             flex 
                             bottom-0 opacity-[0]'
                            
                             ></span>
                        </li>
                        
                    </ul>
                </div>
             </div>
             <div className='right'>
               
             </div>
           </div>
           <Outlet />
        </div>
    )
}

export default Navigation;